$font-montserrat: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Inter, Helvetica, Arial, sans-serif;

.ceiba-hero {
    position: relative;
    display: block;
    width: 100%;
    min-height: 320px;
    color: white;
    background-color: var(--wp--preset--color--contrast, #0a0a0a);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}


.ceiba-hero__backdrop {
    position: absolute;
    inset: 0;
    background: linear-gradient(180deg, rgba(35, 52, 93, .70), rgba(35, 52, 93, .70))
}

.ceiba-hero__inner {
    position: relative;
    z-index: 1;
    max-width: var(--wp--style--global--content-size, 1080px);
    margin: 0 auto;
    padding: clamp(32px, 6vw, 120px) 20px;
    text-align: inherit;
    color: var(--white);
}

.ceiba-hero__title {
    margin: 0 0 12px 0;
    font-size: clamp(28px, 5vw, 56px);
    line-height: 1.1;
}

.ceiba-hero__content {
    font-size: clamp(16px, 2.2vw, 20px);
}

.ceiba-hero .wp-block-buttons {
    margin-top: 16px
}

.wp-block-ceiba-hero-section.alignfull {
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw)
}

.wp-block-ceiba-hero-section.alignwide {
    max-width: 100%
}
